@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Oswald&display=swap);
:root {
    --Oswald: 'Oswald', sans-serif;
    --Bebas: 'Bebas Neue', cursive;
}
* {
    box-sizing: border-box;
    scroll-behavior: smooth !important;
}

html {
    overflow-x: hidden !important;
    padding: 0;
    margin: 0;
}

body {
    overflow-x: hidden !important;
    width: 100%;
    height: 100%;
}

/** =================banner====================*/
#banner {
    background-image: url('https://images.unsplash.com/photo-1620778864482-5f20e3d9745a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2128&q=80');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    color: #ffffff !important;
    position: relative;
}

#banner::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
}

.banner-left,
.banner-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;
}

.banner-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;
}

.banner-right h1 {
    font-size: 5rem;
}

.banner-right h1 img {
    width: 5rem;
}

.banner-left h1 {
    font-weight: bolder;
}

.buttons .btn {
    padding: 10px 35px;
    margin: 50px 20px 0px 0px !important;
}

.buttons .btn-github {
    background-color: bisque;
    color: #000000 !important;
}
.login {
    width: 100%;
    height: 100%;
    background-color: aliceblue;
}
.login-right h1, .signup-right h1 {
    font-weight: bolder;
    padding: 5px 0 15px 0;
    border-bottom: 5px solid #b18800;
}

.login-left h4, .signup-left h4 {
    font-weight: bold;
}


.login-left, .login-right, .signup-left, .signup-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}
.form-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
form label {
    color: #040514 !important;
  }

form input::-webkit-input-placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}

form input:-ms-input-placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}

form input::placeholder {
  font-size: 12px;
  letter-spacing: 0.5px;
}
form input {
    border-radius: 4px;
    height: 2rem;
    width: 18rem;
}
body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box;}

  .form-inline {  
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
  }

  .form-inline label {
    margin: 5px 10px 5px 10px;
  }

  .form-inline input {
    vertical-align: middle;
    margin: 5px 10px 5px 10px;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
  }

  .button-section {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2%;
  }
  .button {
    padding: 10px 20px;
    background-color: rgb(233, 159, 0);
    border: 1px solid #ddd;
    color: rgb(0, 0, 0);
    cursor: pointer;
  }

  .button:hover {
    background-color: rgb(201, 159, 23);
  }

  .remove {
    background-color: rgb(192, 53, 53);
  }
  .remove:hover {
    background-color: rgb(187, 43, 43);
  }
nav {
    background: rgb(11, 13, 15) !important;
    font-family: var(--Oswald);
    letter-spacing: 1px;
}
.navbar-brand {
    font-weight: bolder;
    letter-spacing: 2px;
}

.nav-item .btn {
    margin: 0px 25px;
    padding: 5px 25px !important;
    border-radius: 3px;
}

.btn-login {
    background-color: #fff;
    color: #000000 !important;
}

.btn-register {
    background-color: rgb(233, 159, 0);
    color: #000000 !important;
}
.notfound {
    width: 100%;
    height: 100%;
    background-color: rgb(200, 203, 206);
}

.notfound .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}
.notfound .content h1 {
    font-size: 15rem;
}
.notfound .content h2 {
    font-size: 5em;
}

